@import url(https://fonts.googleapis.com/css?family=Fira+Sans|Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.SignInView{display:flex;flex-direction:column;justify-content:center;align-content:center;margin-top:20%}.SignInView img{width:60px;height:auto;margin:auto;margin-bottom:2rem}.SignInView form{color:black;width:70%;margin:auto;max-width:20rem;display:flex;flex-direction:column;padding:1rem;justify-content:center;align-content:center}.SignInView form label{margin:auto;margin-bottom:1rem}.SignInView form input{margin:auto;margin-bottom:1rem;background:lightgray}.SignInView form .pw{width:60%}


.App{font-family:'Rubik', sans-serif;text-align:center;max-width:40rem;margin:auto}.App input{border:0px solid transparent;background:white;padding:.7rem;font-size:1rem}.App .btn-primary{width:150px;background:gray;padding:.7rem;color:white;font-size:1rem}.App-logo{height:40vmin;pointer-events:none}.App-header{background-color:lightcoral;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

